/* Common styles for current project */
html, body {
  height: 100%;
}

.page {
  background: #f3f4f5;
  color: #191c1f;
  font-display: swap;
  font-family: "Montserrat", Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
}
.page__wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.page__main {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
  overflow: hidden;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 30px;
  padding-left: 30px;
  width: 100%;
  min-width: 320px;
  max-width: 100%;
}
@media (min-width: 1200px) {
  .container {
    max-width: 1540px;
  }
}

img.lazy {
  max-width: 100%;
  height: auto;
}
img.lazy.lazy {
  opacity: 0;
}
img.lazy:not(.initial) {
  transition: opacity 0.3s;
}
img.lazy.initial, img.lazy.loaded, img.lazy.error {
  opacity: 1;
}

img.lazy:not([src]):not([srcset]) {
  visibility: hidden;
}

a {
  color: currentColor;
  text-decoration: none;
  transition: 0.3s ease;
}
a:hover {
  color: currentColor;
  text-decoration: none;
}

